import React, {
  Component
} from 'react'
import Link from 'gatsby-link'

import Container from '../Container'

class ProjectCard extends Component {
  render() {
    return (
      <a href={this.props.link} style={{textDecoration: "none"}}>
        <article className="br2 ba dark-gray b--black-10 mv4 w-100 mw6 center">
            <img src={this.props.imageUrl} className="db w-100 br2 br--top"/>
          <div className="pa2 ph3-ns pb3-ns tl">
            <div className="dt w-100 mt1">
              <div className="dtc">
                <h1 className="f5 f3-ns mv0">{this.props.title}</h1>
              </div>
            </div>
            <p className="f6 lh-copy measure mt2 mid-gray">
              {this.props.children}
            </p>
          </div>
        </article>
      </a>
    )
  }
}

export default ProjectCard



