import React, {
  cloneElement,
  Component
} from "react";
import Link from 'gatsby-link'

import {
  head
} from '../content/home'
import Head from '../components/Head'
import ProjectCard from '../components/ProjectCard'
import Container from '../components/Container'
import FadeInUp from '../components/FadeInUp'
import Slider from 'react-slick';

function NextArrow(props) {
  const {
    className,
    style,
    onClick
  } = props
  return (
    <div
      className={className}
      style={{...style, display: 'block', background: 'none', color: 'black'}}
      onClick={onClick}
    ></div>
  );
}

function PrevArrow(props) {
  const {
    className,
    style,
    onClick
  } = props
  return (
    <div
      className={className}
      style={{...style, display: 'block', color: 'black'}}
      onClick={onClick}
    ></div>
  );
}

const slideSpeed = 500;

const settings = {
  dots: true,
  infinite: true,
  speed: slideSpeed,
  slidesToShow: 1,
  slidesToScroll: 1,
  nextArrow: <NextArrow />,
  prevArrow: <PrevArrow />
};

class PhotosPage extends Component {
  constructor() {
    super();

    this.handleOnWheel = this.handleOnWheel.bind(this);
    this.loading = false;
  }

  renderImageWithUrl(imageUrl) {
    return (
      <div>
            <div style={{position: "absolute", zIndex: 1000}}>Sydney</div>
            <img src={imageUrl} className="w-100"/>
         </div>
    );
  }

  renderVideoWithUrl(videoUrl, caption, divClass) {
    if(!divClass) divClass = "";
    return (
      <div>
        <div className={`tc center ${divClass}`} style={{position: "relative"}}>
          <div style={{position: "absolute", bottom: "20px", zIndex: 1000, height: "30px", width: "100%", backgroundColor: "rgba(0, 0, 0, 0.5)", color: "rgba(255,255,255,0.8)", textAlign: "center", paddingTop: "5px"}}>{caption}</div>
          <video width="100%" src={videoUrl} autoPlay muted playsInline loop/>
         </div>
      </div>
    );
  }

  componentDidMount() {
    // Uncomment to enable scroll to slide.
    // window.addEventListener('wheel', this.handleOnWheel);
    // disableScroll.on();
  }

  handleOnWheel(e) {
    if (!this.slider) return;

    const threshold = 10;

    if (this.loading && Math.abs(e.deltaY) < threshold)
      this.loading = false;
    if (this.loading) return;

    if (e.deltaY < -threshold) {
      this.slider.slickPrev();
      this.loading = true;
    } else if (e.deltaY > threshold) {
      this.slider.slickNext();
      this.loading = true;
    }
  }

  render() {
    return (
      <Container>
        <Head {...head} />
        <Slider ref={c => this.slider = c } {...settings}>
          {/*incepto house*/ /*<div>
            {this.renderVideoWithUrl('https://videos.contentful.com/e4fn4qp8gni1/3wNzUiITj2SUOW8eomoiAY/dcdeba3c43b4dd75bcaaa6175a52d7c7/backyard.mp4')}
          </div>*/}
          <div>
            {this.renderVideoWithUrl('https://videos.contentful.com/e4fn4qp8gni1/1QNfUHD0Ruq4qmuYigmYGu/cf8be4e5d5a27fb4bfa76d678acb063a/sydney.mp4', 'Sydney Sunset, 2017.', 'mw6 mh-75')}
          </div>

          <div>
            {this.renderVideoWithUrl('https://videos.contentful.com/e4fn4qp8gni1/T5HIgCJweAGqga2iKeU60/c6d7812b4b5653d8b1086608f0870357/20852001_1738351596466595_8755167760608133120_n.mp4', 'Sydney Opera House, 2017.', 'mw6 mh-75')}
          </div>
        </Slider>
      </Container>
    );
  }
}

export default PhotosPage



